<template>
    <div>
        <b-table
        class="player-search-table"
        :items="items"
        :fields="fields"
        :filter="filter"
        stacked="md"
        show-wmpty
        width=300px>

        <template #cell(image)="row">
            <img :src="row.item.image"
            class="card-img-bottom"
            style="height: 100px; width: auto"
             v-on:click.stop="PlayerPage(row.item.playerID)"/>
        </template>

        <template #cell(name)="row">
            <a href="#" v-on:click="PlayerPage(row.item.playerID)"> {{ row.item.name }}</a>
        </template>

        </b-table>
    </div>
</template>

<script>
    export default {
        data(){
            return{
                fields:[
                   {key: 'playerId', label: 'Player ID'} ,
                   {key: 'name', label: 'Player Full Name', sorted:true},
                   {key: 'position', label: 'Position', sorted:true},
                   {key: 'imageUrl', label: 'Player Image'},
                ],
                filter: null,
            };
        },
        props:{
            items:{
                type: Object,
                required: true
            }
        },
        methods:{
            PlayerPage(playerId){
                this.$router.push('/player' + String(playerId))
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>