<template>
    <div> 
      <b-container> 
    <h1 class="title">Search Page</h1>
    <b-form @submit.prevent="onSearch" @reset.prevent="onReset">
        <div v-if="{query}">
            <b-row>
                <b-col>
                    <button @click ="sort_players"> Sort Players </button>
                </b-col>
                <b-col>
                    <button @click ="sort_teams"> Sort Teams </button>
                </b-col>
                <b-col>
                    <b-form-input class="team-filter-input" v-model="filter_pos"></b-form-input>
                    <b-button v-on:click="PositionFilter">filter Player Position</b-button>
                </b-col>
                <b-col>
                    <b-form-input class="team-filter-input" v-model="filter_team"></b-form-input>
                    <b-button v-on:click="TeamFilter">filter Player Team</b-button>
                </b-col>
                <b-col> 
                    <b-button v-on:click="Order">Order Results</b-button> 
                </b-col>
            <br/><br/><br/><br/>
            </b-row> 
        </div>
    <b-input-group prepend="Search Query:" id="search-input">
      <b-form-input v-model="query" id="query"></b-form-input>
      <b-input-group-append>
        <b-button 
        type="submit"
        variant="info"
        >Search</b-button>
      </b-input-group-append>
    </b-input-group>
    </b-form>
      <br/>
      Your search Query: {{ query }}
      <br/>
        <span v-if="{query}">
        <h4>Players:</h4>
            <SearchPlayers :items="Players"></SearchPlayers>
            <SearchTeams :items="Teams"> </SearchTeams>
            <!-- <PlayerPreview
                v-for="p in players"
                :playerId="p.id"
                :name="p.name"
                :imageUrl="p.image"
                :position="p.position"
                :src="p.image"
                :key="p.name">
            </PlayerPreview>
            <h4>Teams:</h4>
            <TeamPreview  v-for="t in teams"
                :name="t.name"
                :imageUrl="t.logo_path"
                :src="t.logo_path"
                :key="t.name">
            </TeamPreview> -->
        </span>
    </b-container>
    </div>
</template>

<script>
// import PlayerPreview from "./PlayerPreview"; 
// import TeamPreview from "./TeamPreview";

import SearchPlayers from "./SearchPlayers";
// import SearchTeams from "./SearchTeams";
export default {
    name: "Search",
    components:{
        // PlayerPreview,
        // TeamPreview,
        SearchPlayers,
        // SearchTeams
    },
    data(){
        return{
            players: [],
            teams: [],
            query: "",
            filter_team: ""
        };
    },
    methods:{
        async newSearch(){
            console.log(this.query);
            console.log("start searching at the server");
            try{
                const response = await this.axios.get(
                    `http://localhost:3003/search/${this.query}`,);
                const players = response.data.players;
                const teams = response.data.teams;
                console.log(teams);

                this.players = [];
                this.players.push(...players);
                this.teams = [];
                this.teams.push(...teams);
            }catch (error){
                console.log("error in search");
                console.log(error);
            }
        },

        onSearch(){
           console.log("neaSearch method was called");
           this.newSearch(); 
        },

        onReset(){
            console.log("reset before new search");
            this.$v.$reset();
        },

        // sort_players: function () {
        // this.data.players = _.orderBy(this.data.players, 'name', 'asc')
        // }
        TeamFilter(){
            this.$emit('this.players', 'filter_team', this.filter_team)
        }
    }
        
}
</script>

<style lang="scss" scoped>


</style>